.product-card {
  background-color:  #5f5d5d; /* Dark gray background color */
  border: 1px solid #ccc;
  padding: 10px;
  width: 250px; /* Set a fixed width for the card */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
  border-radius: 8px; /* Add rounded corners */
  margin-bottom: 20px; /* Add margin to the bottom of the card */
}

.product-card img {
  max-width: 100%; /* Make the image responsive */
  max-height: 200px; /* Set a maximum height for the image */
  display: block; /* Ensure the image is a block element */
  margin: 0 auto 10px; /* Center the image horizontally and add bottom margin */
}

.product-card h2 {
  margin-top: 0;
  font-size: 18px; /* Increase font size for the title */
}

.product-card p {
  margin: 5px 0;
  font-size: 14px; /* Adjust font size for other text */
}
.product-card.highlighted {
  border: 2px solid green;
  box-shadow: 0 0 5px green;
}
